html{
    font-size: 10px;
    font-family: 'sans-serif';
}

p{
    font-size: 1.5rem;
    line-height: 1.5;
}

h1{
    font-family: 'billabong';
    text-align: center;
    font-size: 13rem;
    font-weight: 100;
    margin: 2rem 0;
    letter-spacing: -1px;
    color: black;
}

@font-face{
    font-family: 'billabong';
    src: url('./FontsFree-Net-Billabong.ttf');
    font-weight: bold;
}

.photoGrid{
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
}

.figure {
    flex-basis: calc(33.333% - 4rem);
    border: 1px solid #d3d3d3;
    padding: 2rem;
    flex-grow: 1;
    margin: 0 2rem 2rem 2rem;
}

.photo {
    width: calc(100% + 4rem);
    margin: -2rem 0rem 0rem -2rem;
}

.button-container{
    display: flex;
    justify-content: center;
}

.remove-button{
    flex-basis: 48%;
    color: red;
    font-size: 15px;
    border: 2px solid #f2f2f2;
    line-height: 2;
    padding: 5px;
}

.addIcon{
    margin: 20px auto;
    display: table;
    padding: 30px;
    background: url("https://image.flaticon.com/icons/svg/60/60740.svg");
    background-size: contain;
    border: none;
}

.form{
    margin: 0 auto;
    max-width: 360px;
    padding: 45px;
    text-align: center;
}

.form input{
    font-family: sans-serif;
    background: #f2f2f2;
    width: 100%;
    border: none;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    margin: 0 0 15px ;
}

.form button{
    font-family: sans-serif;
    text-transform: uppercase;
    background: #065784;
    width: 100%;
    border: none;
    padding: 15px;
    color: white;
    font-size:  14px;
    cursor: pointer;
}

.form button:hover{
    background: #7BB589;
}

button:hover{
    border-color: black;
}